import React from 'react';
import SkillsCard from '../SkillsCard/SkillsCard'
import "./WhatIDo.css";
import {skillsinfo_column1, skillsinfo_column2} from "../../skills"

function generateSkillsCards(skillsinfo){
    return(
        <SkillsCard 
        key={skillsinfo.id}
        class={skillsinfo.class}
        title={skillsinfo.title}
        info={skillsinfo.info}
        />
    );
}

function WhatIDo(){
    return(
        <div className="App Container WhatIDo fade-in">
            <div className="Container-centered">
                <h3 className="text-uppercase text-orange title">What I do</h3>
                <div className="spacer-small"></div>
                <h1 className="Subheader-text">I have a breath of experiecne in the tech.</h1>
                <div className="spacer"></div>
                <div className="border-bottom--black border-small"></div>
                <div className="spacer"></div>
                <div className="Skills">
                    <div className="row">
                        {skillsinfo_column1.map(generateSkillsCards)}
                    </div>
                    <div className="row">
                        {skillsinfo_column2.map(generateSkillsCards)}
                    </div>
                </div>
             </div>
          
        </div>
    );
}

export default WhatIDo;

